<template>
  <div class="HomeTab">
    <h1>Welcome to replifactory!</h1>
    <p>To start using the app, navigate to the 'Device' tab and start controlling your device.</p>
    <input
        v-model="keyboardInput.value"
        class="input"
        @focus="isInputSelected = true"
        placeholder="Tap on the virtual keyboard to start"
    >
    <div class="keyboard-container" v-click-outside="hideKeyboard">
      <SimpleKeyboard
          v-show="isInputSelected"
          :input="keyboardInput.value"
          @input="updateInput"
      />
    </div>
  </div>
</template>

<script>
import SimpleKeyboard from "../SimpleKeyboard";
import { mapState, mapMutations } from 'vuex';

export default {
  name: "HomeTab",
  components: {
    SimpleKeyboard
  },
  data: () => ({
    isInputSelected: false
  }),
  computed: {
    ...mapState(['keyboardInput']),
  },
  methods: {
    ...mapMutations(['setKeyboardInput']),
    hideKeyboard() {
      this.isInputSelected = false;
    },
    updateInput(value) {
      this.setKeyboardInput({ value, active: this.keyboardInput.active });
    },
  },
};
</script>


<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.keyboard-container {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  max-width: 1024px;
  margin-left: auto;
  margin-right: auto;
}

body {
  /* Adjust padding-bottom to the actual height of your keyboard */
  padding-bottom: 200px;
}
</style>
