<template>
  <div class="DeviceControl">
        <label>
          Calibration mode:
          <input type="checkbox" :checked="calibrationModeEnabled" @change="toggleCalibrationMode" />
        </label>
        <PumpControl />
        <ValveControl />
        <StirrerControl />
        <ODControl />
  </div>
</template>


<script>
import PumpControl from './PumpControl';
import ValveControl from './ValveControl';
import StirrerControl from './StirrerControl';
import ODControl from './ODControl';

import {mapState, mapMutations, mapActions} from 'vuex';



export default {
  components: {
    PumpControl,
    ValveControl,
    StirrerControl,
    ODControl,
  },
  computed: {
    ...mapState('device',['calibrationModeEnabled','stirrers','pumps','valves','ods'])
  },
  mounted() {
    // Listen for keyboard events
    this.getAllDeviceData().then(() => {

    });
  },
  methods: {
    ...mapMutations('device',['toggleCalibrationMode']),
    ...mapActions('device',['getAllDeviceData']),

    hideKeyboard() {
      this.isInputSelected = false;
    },
    updateInput(value) {
      this.setKeyboardInput({ value, active: this.keyboardInput.active });
    },
  },
};
</script>

<style scoped>
  .row{
    max-width: 1024px;
    margin: 0 auto;
  }
</style>